import React, { useRef, useState, useEffect} from "react";
import { FaBars } from "react-icons/all";
import { IconContext } from "react-icons";
import {Link} from "react-router-dom";


const DropDownMenu = ()=>{

    const [showMenu, setShowMenu] = useState( false );
    const [dropDownStyle, setDropDownStyle] = useState({});
    const wrapperRef = useRef(null);

    const handleClickOutside = event => {
        if (wrapperRef.current && ! wrapperRef.current.contains(event.target)){
            setShowMenu(false);
        }
    }

    useEffect(()=>{
        if(showMenu === true) {
            console.log("Setting dropdown style to block");
            setDropDownStyle({
                display:"block"
            });
        } else {
            console.log("Setting dropdown style to none");
            setDropDownStyle({
                display:"none"
            });
        }

        document.addEventListener("click", handleClickOutside);
        return () => {
            document.removeEventListener("click", handleClickOutside);
        };
    }, [ showMenu, wrapperRef ]);

    const flipMenu = ()=>{
        console.log("Flipping menu to" + !showMenu);
        console.log("Showmenu: " + showMenu);
        setShowMenu(!showMenu);
    };

    return(
        <IconContext.Provider value={{
            color:  "white",
            verticalAlign: "center",
            size: 25
        }}>
            <div className="menu" ref={wrapperRef}>
                <button onClick={() => flipMenu()}><FaBars /></button>
                <div style={dropDownStyle} id="dropdownmenu" className="dropdown-list">
                    <Link to="/">Home</Link>
                    <Link to="/about">About</Link>
                    <Link to="/recipees">Recipes</Link>
                    <Link to="/articles-list">Articles</Link>
                    <Link to="/videos">Videos</Link>
                </div>
            </div>
        </IconContext.Provider>
    );
}

export default DropDownMenu;

