import React from 'react';
import HealthyDreamzSlideshow from "../components/HealthyDreamzSlideshow";
import slideshow_registry from "../components/slideshow_registry.json";

const getSlideshowImages = () =>{
    var slides = [];
    for (const [key, value] of Object.entries(slideshow_registry)) {
        try{
            console.log(`${key}: ${value}`);
        }catch (e) {
            console.log(e);
        }
        slides.push(value);
    }
    return slides;
}

const HomePage = () =>{

    const slides = getSlideshowImages();
    console.log(slides);
    return(
        <>
            <HealthyDreamzSlideshow slides={slides} interval={5000}/>
            <br/>
            <h1 className="center cursive bold">Health and Nutrition News</h1>
            <p>"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>
        </>
    )};

export default HomePage;