import React, { useState, useEffect } from "react";
import "../styles/slideshow.css";

const HealthyDreamzSlideshow = ({ slides=[], interval=3000 }) => {

    const [ currentSlide, setCurrentSlide ] = useState(0);
    const [ currentSlideStyle, setCurrentSlideStyle ] = useState({});


    useEffect(()=>{
        setCurrentSlideStyle({
            backgroundImage: "url('" + slides[currentSlide] + "')"
        });
        console.log("Current slide: " + slides[currentSlide]);

        const loop = setInterval(()=>{
            if(currentSlide === 0){
                setCurrentSlide(slides.length-1);
            }else{
                setCurrentSlide(currentSlide-1);
            }
        }, interval);
        return () => clearInterval(loop);

    }, [slides, currentSlide, interval]);

    function next(){
        if(currentSlide>0){
            setCurrentSlide(currentSlide-1);
        }else{
            setCurrentSlide(slides.length-1);
        }
    }

    function previous(){
        if(currentSlide === slides.length-1){
            setCurrentSlide(0);
        }else{
            setCurrentSlide(currentSlide+1);
            console.log("current slide: " + currentSlide);
        }
    }
    return(
        <section className="healthydreamzslideshow">
            <div className="slide-frame">
                <section className="slide current-slide">
                    <div style={currentSlideStyle} className="slide-thumb"/>
                </section>
            </div>
        </section>
    )
}

export default HealthyDreamzSlideshow;