import './App.css';
import React, { Component } from "react";
import {
    Router,
    Route,
    Switch,
} from "react-router-dom";
import NavBar from "./components/NavBar";
import HomePage from "./pages/HomePage";
import AboutPage from "./pages/AboutPage";
import ArticlesList from "./pages/ArticlesListPage";
import ArticlePage from "./pages/ArticlePage";
import NotFoundPage from "./pages/NotFoundPage";
import { useAuth0 } from "@auth0/auth0-react";
import Loading  from "./components/Loading";
//import axios from "axios";
import history from "./history";

class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            title: "HealthyDreamz",
            news_uri: '/latest_news',
            brandname: "HealthyDreamz",
            articles: []
        }
    }

    async componentDidMount() {
        //get the latest news for slide show
        console.log("Mounted Application");
        //const latest_news = await axios.get(this.state.news_uri);
    }

    render()
    {
        const { loading } = useAuth0;

        if(loading) {
            return <Loading />
        }

        return (
            <React.StrictMode>
                <Router history={history}>
                    <div className="App">
                        <NavBar brandname={this.state.brandname}/>
                        <div id="page-body">
                            <Switch>
                                <Route path='/' component={HomePage} exact/>
                                <Route path='/about' component={AboutPage}/>
                                <Route path='/articles-list' component={ArticlesList}/>
                                <Route path='/article/:name' component={ArticlePage}/>
                                <Route component={NotFoundPage}/>
                            </Switch>
                        </div>
                    </div>
                </Router>
            </React.StrictMode>
        );
    }
}

export default App;
